import { ref, reactive } from 'vue'
export function useTimes() {
  const date = new Date()
  const curTime = reactive({
    year: date.getFullYear(),
    month: date.getMonth(),
    day: date.getDate(),
    hours: date.getHours(),
    minutes: date.getMinutes(),
    seconds: date.getSeconds(),
  })
  const timer = setInterval(() => {
    const date = new Date()
    curTime.year = date.getFullYear()
    curTime.month = date.getMonth()
    curTime.day = date.getDate()
    curTime.hours = date.getHours()
    curTime.minutes = date.getMinutes()
    curTime.seconds = date.getSeconds()
  }, 1000)

  return { curTime, timer }
}
