import { defineStore, storeToRefs } from 'pinia'
import mainStore from './mainStore'
import localCache from '../utils/cache'

export default defineStore('group', {
  state() {
    return {
      groupId: 1,
      group: [
        {
          id: 0,
          title: '视频',
          list: [
            {
              id: 7,
              webName: '优酷',
              webUrl: 'www.youku.com',
              icon: 'https://files.codelife.cc/website/youku.svg',
              defineIcon: {
                isUseDefineIcon: false,
                color: '#FFF',
                words: '优酷',
              },
            },
          ],
        },
      ],
    }
  },
  getters: {
    groupCount(): number {
      return this.group.length
    },
  },
  actions: {
    addAppInGroup(app: any, groupId: number, appIndex: number) {
      this.group.forEach((item, index, array) => {
        if (item.id === groupId) {
          this.group[index].list.splice(appIndex, 0, app)
        }
      })
      localCache.setCache('group', this.group)
      localCache.setCache('groupId', this.groupId)
    },
    updateGroupList(newList: any, groupId: number) {
      this.group.forEach((item, index) => {
        if (item.id === groupId) {
          this.group[index].list = newList
        }
      })
      localCache.setCache('group', this.group)
      localCache.setCache('groupId', this.groupId)
    },
    updateGroupTitle(groupId: number, title: string) {
      this.group.forEach((item, index) => {
        if (item.id === groupId) {
          this.group[index].title = title
        }
      })
      localCache.setCache('group', this.group)
      localCache.setCache('groupId', this.groupId)
    },
    addGroup() {
      this.group.push({
        id: this.groupId,
        title: '默认分组',
        list: [],
      })
      this.groupId++
      localCache.setCache('group', this.group)
      localCache.setCache('groupId', this.groupId)
    },
    dissBandGroup(id: number) {
      this.group.forEach((item, index, array) => {
        if (item.id === id) {
          item.list.forEach((item, index, array) => {
            console.log(item, index)
            const newApp = item
            newApp.id = mainStore().appId
            mainStore().addApp(newApp, mainStore().appList.length)
            console.log('groupStore更新了', this.group)
            console.log('mainStore更新了', mainStore().appList)
          })
          array.splice(index, 1)
        }
      })
      localCache.setCache('group', this.group)
      localCache.setCache('groupId', this.groupId)
      localCache.setCache('appList', mainStore().appList)
      localCache.setCache('appId', mainStore().appId)
    },
    getlocalCache() {
      const groupId = localCache.getCache('groupId')
      const group = localCache.getCache('group')
      if (groupId) this.groupId = groupId
      if (group) this.group = group
    },
  },
})
