// 判断双击单击事件
let treeClickCount = 0
const handleNodeClick = (fn1, fn2) => {
  // data为被点击节点数据
  // 记录点树节点击次数
  treeClickCount++
  if (treeClickCount > 2) return
  setTimeout(() => {
    if (treeClickCount == 1) {
      // 进行单击事件处理
      // console.log('单击事件', data)
      if (fn1) fn1()
    } else if (treeClickCount == 2) {
      // 进行双击事件处理
      // console.log('双击事件', data)
      // isShowTitleInput.value = true
      if (fn2) fn2()
    }
    treeClickCount = 0
  }, 300)
}
const debounceFn = function (Fn) {
  let timer = null
  return function () {
    clearTimeout(timer)
    timer = setTimeout(() => {
      Fn.apply(this, arguments)
    }, 1000)
  }
}
export { handleNodeClick, debounceFn }
