import { defineStore } from 'pinia'
import localCache from '../utils/cache'
export default defineStore('main', {
  // other options...
  state() {
    return {
      curApp: {},
      appId: 7,
      appList: [
        {
          id: 0,
          webName: '知乎',
          icon: 'https://files.codelife.cc/website/zhihu.svg',
          webUrl: 'www.zhihu.com',
          defineIcon: {
            isUseDefineIcon: false,
            color: '',
            words: '',
          },
        },
        {
          id: 1,
          webName: '哔哩哔哩',
          icon: 'https://files.codelife.cc/website/bilibili2.svg',
          webUrl: 'www.bilibili.com',
          defineIcon: {
            isUseDefineIcon: false,
            color: '',
            words: '',
          },
        },
        {
          id: 2,
          webName: '掘金',
          icon: 'https://files.codelife.cc/website/juejin.svg',
          webUrl: 'www.juejin.com',
          defineIcon: {
            isUseDefineIcon: false,
            color: '',
            words: '',
          },
        },
        {
          id: 3,
          webName: '新浪微博',
          icon: 'https://files.codelife.cc/website/weibo.svg',
          webUrl: 'www.weibo.com',
          defineIcon: {
            isUseDefineIcon: false,
            color: '',
            words: '',
          },
        },
        {
          id: 4,
          webName: '爱奇艺',
          icon: 'https://files.codelife.cc/website/iqiyi.svg',
          webUrl: 'www.iqiyi.com',
          defineIcon: {
            isUseDefineIcon: false,
            color: '',
            words: '',
          },
        },
        {
          id: 5,
          webName: '网易云音乐',
          icon: 'https://files.codelife.cc/website/music163.svg',
          webUrl: 'music.163.com/',
          defineIcon: {
            isUseDefineIcon: false,
            color: '',
            words: '',
          },
        },
        {
          id: 6,
          webName: '优酷',
          icon: 'https://files.codelife.cc/website/youku.svg',
          webUrl: 'www.youku.com',
          defineIcon: {
            isUseDefineIcon: false,
            color: '',
            words: '',
          },
        },
      ],
    }
  },
  actions: {
    addApp(app: any, index: number) {
      console.log('添加了')
      // 不能简单的push，找到要加载的地方插入
      console.log('插入位置', index)
      this.appList.splice(index, 0, app)
      this.appId++
      localCache.setCache('appList', this.appList)
      localCache.setCache('appId', this.appId)
    },
    removeApp(app: any) {
      this.appList.forEach((item, index) => {
        if (item.id === app.id) {
          this.appList.splice(index, 1)
        }
      })
      localCache.setCache('appList', this.appList)
      localCache.setCache('appId', this.appId)
    },
    updateApp(newAppInfo: any) {
      this.appList.forEach((item, index) => {
        if (item.id === newAppInfo.id) {
          item.webName = newAppInfo.webName
          item.webUrl = newAppInfo.webUrl
          item.defineIcon = newAppInfo.defineIcon
        }
      })
      localCache.setCache('appList', this.appList)
      localCache.setCache('appId', this.appId)
    },
    updateAppList(newList: any) {
      localCache.setCache('appList', this.appList)
      localCache.setCache('appId', this.appId)
    },
    getLocalCache() {
      const appList = localCache.getCache('appList')
      const appId = localCache.getCache('appId')
      if (appList) this.appList = appList
      if (appId) this.appId = appId
    },
  },
})
