import { App } from 'vue'
import {
  ElButton,
  ElDrawer,
  ElInput,
  ElRadio,
  ElRadioGroup,
  ElCard,
  ElDialog,
  ElRow,
  ElCol,
  ElScrollbar,
  ElFormItem,
  ElForm,
  ElTabs,
  ElTabPane,
  ElCollapse,
  ElCollapseItem,
  ElIcon,
  ElUpload,
  ElImage,
  ElColorPicker,
} from 'element-plus'
const elements = [
  ElButton,
  ElInput,
  ElRadioGroup,
  ElRadio,
  ElDrawer,
  ElCard,
  ElDialog,
  ElRow,
  ElCol,
  ElScrollbar,
  ElFormItem,
  ElForm,
  ElTabs,
  ElTabPane,
  ElCollapse,
  ElCollapseItem,
  ElIcon,
  ElUpload,
  ElImage,
  ElColorPicker,
]

export function registerElement(app: App) {
  for (let element of elements) {
    app.component(element.name, element)
  }
}
